.container{
  display: flex;
  align-items: center;
  flex-direction: column;
}

.title{
  display: flex;
  justify-content: center;
  font-family: 'Mansalva', cursive;
  font-size: 25px;
  margin-top: -30px;
}

.subTitle{
  display: flex;
  flex-direction: row;
  font-family: 'Mansalva', cursive;
  font-size: 20px;
}

.info{
  margin-left: 20px;
  margin-right: 20px;
}

.refresh{
  height: 18px;
  width: auto;
  margin-top: 23px;
  cursor: pointer;
}

.board {
  max-width: 750px;
  height: 750px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  background-color: #656465;
  border-radius: 5px;
  box-shadow: 7px 7px 5px 5px rgba(0, 0, 0, 0.171);
}

.lose{
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  font-size: 150px;
  font-family: 'Mansalva', cursive;
  color: rgb(12, 126, 192);
  margin-top: -400px;
  z-index: 1;
  background-color: rgba(184, 184, 184, 0.801);
  padding-top: 79px;
  padding-bottom: 83px;
  border-radius: 5px;
}

.refresh2{
  height: auto;
  width: 80px;
  margin-top: 20px;
  cursor: pointer;
}

.hide {
  display: none;
}